<template>
	<div>
		<a-card>
			<a-row :gutter="20">
				<a-col :span="6">
					<a-input-search 
					v-model="queryParam.username"
					allowClear placeholder="" 
					enter-button 
					@search="getUserList" />
				</a-col>
				<a-col :span="4">
					<a-button type="primary" @click="AddUserVisible = true"></a-button>
				</a-col>
			</a-row>
			<a-table
				:columns='columns' 
				rowKey='ID'
				:pagination="pagination"
				:dataSource="userlist"
				bordered
				@change="handleTableChange"
			>
			<span slot="role" slot-scope="role">{{role == 1 ? "":""}}</span>
			<template slot="action" slot-scope="data">
				<div class="actionSlot">
				<a-button type="info" icon="info" style="margin-right:15px" @click="ChangePassword(data.ID)"></a-button>
				<a-button type="primary" icon="edit" style="margin-right:15px" @click="editUser(data.ID)"></a-button>
				<a-popconfirm title="" ok-text="" cancel-text="" @confirm="delUser(data.ID)">
				  <a-button icon="delete" type="danger"></a-button>
				</a-popconfirm>
			</div>
			</template>
			</a-table>
		</a-card>
		
		<!--  -->
		<a-modal
      title=""
      :visible="AddUserVisible"
      @ok="AddUserOk"
      @cancel="addUserCanal"
			closable
    >
      <a-form-model :model="userinfo" :rules="userRules" ref="addUserRef">
				<a-form-model-item label="" prop="username">
					<a-input v-model="userinfo.username"></a-input>
				</a-form-model-item>
				<a-form-model-item label="" prop="password">
					<a-input-password v-model="userinfo.password">

					</a-input-password>
				</a-form-model-item>
				<a-form-model-item label="" prop="checkpass">
					<a-input-password v-model="userinfo.checkpass">

					</a-input-password>
				</a-form-model-item>
				
			</a-form-model>
    </a-modal>

		<!--  -->
		<a-modal
      title=""
      :visible="editUserVisible"
      @ok="editUserOk"
			width="60%"
      @cancel="editUserCanal"
			closable
    >
      <a-form-model :model="userinfo" :rules="userRules" ref="addUserRef">
				<a-form-model-item label="" prop="username">
					<a-input v-model="userinfo.username"></a-input>
				</a-form-model-item>

				<a-form-model-item label="">
          <a-switch
            :checked="IsAdmin"
            checked-children=""
            un-checked-children=""
            @change="adminChange"
          />
        </a-form-model-item>
				
			</a-form-model>
    </a-modal>

		<!--  -->
		<a-modal
      closable
      title=""
      :visible="changePasswordVisible"
      width="60%"
      @ok="changePasswordOk"
      @cancel="changePasswordCancel"
      destroyOnClose
    >
      <a-form-model :model="userinfo" :rules="userRules" ref="addUserRef">
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password v-model="userinfo.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkpass">
          <a-input-password v-model="userinfo.checkpass"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

	</div>
</template>

<script>
const columns = [
	{
    title: 'ID',
    dataIndex: 'ID',
    key: 'id',
    width: '10%',
		align:'center'
  },
	{
    title: '',
    dataIndex: 'username',
    key: 'username',
    width: '20%',
		align:'center'
  },
	{
    title: '',
    dataIndex: 'role',
    key: 'role',
    width: '20%',
		scopedSlots:{customRender:'role'},
		align:'center'
  },
	{
    title: '',
    key: 'action',
    width: '30%',
		scopedSlots:{customRender:'action'},
		align:'center'
  },
]
export default {
	data(){
		return{
			columns,
			changePasswordVisible: false,
			// 
			pagination: {
        pageSizeOptions: ['5', '10', '20'],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
			queryParam: {
				username:'',
        pagesize: 5,
        pagenum: 1,
      },
			userlist:[],
			AddUserVisible:false,
			editUserVisible:false,
			userinfo:{
				id:0,
				username:'',
				password:'',
				checkpass:'',
				role:2
			},
			userRules:{
				username: [
          {
            validator: (rule, value, callback) => {
              if (this.userinfo.username == '') {
                callback(new Error(''))
              }
              if ([...this.userinfo.username].length < 4 || [...this.userinfo.username].length > 12) {
                callback(new Error('412'))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.userinfo.password == '') {
                callback(new Error(''))
              }
              if ([...this.userinfo.password].length < 6 || [...this.userinfo.password].length > 20) {
                callback(new Error('620'))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
        ],
				checkpass: [
          {
            validator: (rule, value, callback) => {
              if (this.userinfo.checkpass == '') {
                callback(new Error(''))
              }
              if (this.userinfo.password !== this.userinfo.checkpass) {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
        ],



			},
		}
	},
	created(){
		this.getUserList()
	},
	computed: {
		// 
    IsAdmin: function () {
      if (this.userinfo.role === 1) {
        return true
      } else {
        return false
      }
    }
	},
	methods:{
		// 
		async getUserList(){
			const { data:res } = await this.$http.get('api/v1/user/', {
				params:{
					username: this.queryParam.username,
					size: this.queryParam.pagesize,
					page: this.queryParam.pagenum,
					},
				})
			if(res.code != 200)return this.$message.error(res.msg)
			this.userlist = res.data
			this.pagination.total = res.total
		},
		// 
		handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pagesize = pagination.pageSize
      this.queryParam.pagenum = pagination.current

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getUserList()
    },
		// 
		async delUser(id){
		  const { data: res } = await this.$http.delete(`api/admin/user/${id}/`)
			if(res.code != 200)return this.$message.error(res.msg)
			this.queryParam.pagenum = 1
			this.$message.success('')
			this.getUserList()
		},

		AddUserOk(){
			this.$refs.addUserRef.validate(async (vaild)=>{
				if(!vaild)return this.$message.error("")
				const { data: res } = await this.$http.post("api/v1/user/",{
					"username":this.userinfo.username,
					"password":this.userinfo.password,
					"role": this.userinfo.role,
					})
				if (res.code != 200) return this.$message.error(res.msg)
				// 
				this.$refs.addUserRef.resetFields()
				// 
        this.AddUserVisible = false
        this.$message.success('')
        this.getUserList()
			})
		},
		// role
		adminChange(checked){
      if (checked) {
        this.userinfo.role = 1
      } else {
        this.userinfo.role = 2
      }
		},
		// 
		addUserCanal(){
			this.$refs.addUserRef.resetFields()
			this.AddUserVisible = false
		},
		// 
		async editUser(id){
			this.editUserVisible = true
			const {data: res } = await this.$http.get(`/api/v1/user/${id}/`)
			this.userinfo = res.data
			this.userinfo.id = id
		},
		// 
		editUserOk(){
			this.$refs.addUserRef.validate(async (vaild)=>{
				if(!vaild)return this.$message.error("")
				const { data: res } = await this.$http.put(`/api/admin/user/${this.userinfo.id}/`,{
					"username":this.userinfo.username,
					"role": this.userinfo.role,
					})
				if (res.code != 200) return this.$message.error(res.msg)
				// 
				this.$refs.addUserRef.resetFields()
				// 
        this.editUserVisible = false
        this.$message.success('')
        this.getUserList()
			})
		},
		// 
		editUserCanal(){
			this.$refs.addUserRef.resetFields()
			this.editUserVisible = false
		},
		// 
		ChangePassword(id){
			this.changePasswordVisible = true
      this.userinfo.id = id
		},
		// 
		changePasswordOk() {
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(`/api/admin/editpass/${this.userinfo.id}/`,{
				"password":this.userinfo.password
			})
        if (res.code != 200) return this.$message.error(res.msg)
        this.changePasswordVisible = false
        this.$message.success('')
        this.getUserList()
      })
    },
		// 
		changePasswordCancel() {
      this.$refs.addUserRef.resetFields()
      this.changePasswordVisible = false
      this.$message.info('')
    },


	}

}
</script>

<style>
.actionSlot{
	display: flex;
	justify-content: center;
}

</style>