<template>
	<div>
		<a-card>
			<h3>{{id?'':''}}</h3>
		<a-form-model :model="artInfo" ref="artInfoRef" :rules="artInfoRules" :hideRequiredMark="true">
			<a-form-model-item label="" prop="title">
				<a-input style="width:300px" v-model="artInfo.title"></a-input>
			</a-form-model-item>
			<a-form-model-item 
			style="width:200px"
			prop="cid"
			label="">
				<a-select v-model="artInfo.cid" placeholder="" @change="cateChange">
					<a-select-option v-for="item in Catelist" :key="item.id" :value="item.id">{{item.name}}</a-select-option>
				</a-select>
			</a-form-model-item>
			<a-form-model-item label="" prop="desc">
				<a-input type="textarea" v-model="artInfo.desc"></a-input>
			</a-form-model-item>
			<a-form-model-item label="" prop="img"> 
				<a-upload
					name="file"
					:multiple="false"
					:action="uploadUrl"
					:headers="headers"
					@change="upChange"
					listType="picture"
				>
					<a-button> <a-icon type="upload" />  </a-button>
					<br/>
					<template v-if="id">
						<!-- <img v-if="artInfo.img" :src="baseurl+artInfo.img" style="width:120px;height:100px"> -->
						<img v-if="artInfo.img" :src="'http://localhost:8080/'+artInfo.img" style="width:120px;height:100px">
					</template>
				</a-upload>
			</a-form-model-item>
			<a-form-model-item label="" prop="content">
				<mavon-editor
            ref="md"
            placeholder="..."
            :boxShadow="false"
            style="z-index:1;border: 1px solid #d9d9d9;height:50vh"
            v-model="artInfo.content"
            :toolbars="toolbars"
						@imgAdd="$imgAdd"
						@imgDel="$imgDel"
          />
			</a-form-model-item>
			<a-form-model-item>
				<a-button type="primary" 
				style="margin-right:15px" 
				@click="artOk(artInfo.id)"
				>
				{{artInfo.id?"":''}}
				</a-button>
				<a-button type="danger" @click="addCancel"></a-button>
			</a-form-model-item>
		</a-form-model>
		</a-card>
	</div>
</template>

<script>
import {Url} from "../../plugins/http"
export default {
	props:['id'],
	data(){
		return{
			// baseurl:'',
			artInfo:{
				id:0,
				title:'',
				cid:undefined,
				desc:'',
				content:'',
				img:''
			},
			Catelist:[],
			uploadUrl:Url+'/api/admin/file/',
			headers:{},
			artInfoRules: {
        title: [{ required: true, message: '', trigger: 'change' }],
        cid: [{ required: true, message: '', trigger: 'change' }],
        desc: [
          { required: true, message: '', trigger: 'change' },
          { max: 120, message: '120', trigger: 'change' },
        ],
        content: [{ required: true, message: '', trigger: 'change' }],
      },
			// markdown
			toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: false, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      }
		}
	},
	created(){
		this.getCateList()
		this.headers = {Authorization:`Bearer ${window.sessionStorage.getItem('token')}`}
		if(this.id){
			this.getArtInfo(this.id)
		}
	},
	methods:{
		// 
		async getArtInfo(id){
			const {data: res} = await this.$http.get(`api/v1/article/${id}/`)
			if(res.code != 200) return this.$$message.error(res.msg)
			this.artInfo = res.data
			this.artInfo.id = res.data.ID
			this.artInfo.img = res.data.img
			// v-if
			// this.baseurl = "http://localhost:8081/"
		},
		// 
    async getCateList() {
      const { data: res } = await this.$http.get('api/v1/category/',{params: {
          size: 100,
          page: 1,
        },})
      if (res.code !== 200) return this.$message.error(res.msg)
      this.Catelist = res.data
    },
		cateChange(val){
			this.artInfo.cid = val
		},
		upChange(info){
			if (info.file.status !== 'uploading') {
        
      }
      if (info.file.status === 'done') {
        this.$message.success('');
				const imgUrl = info.file.response.data
				this.artInfo.img = imgUrl
      } else if (info.file.status === 'error') {
        this.$message.error(``);
      }
		},
		// 
		artOk(id){
			
			this.$refs.artInfoRef.validate(async (valid) => {
				if (!valid) return this.$message.error('')
				if (id === 0) {
          const { data: res } = await this.$http.post('/api/admin/article/', this.artInfo)
          if (res.code !== 200) return this.$message.error(res.msg)
          this.$router.push('/artlist')
          this.$message.success('')
        } else {
          const { data: res } = await this.$http.put(`/api/admin/article/${id}/`, this.artInfo)
          if (res.code !== 200) return this.$message.error(res.msg)
          this.$router.push('/artlist')
          this.$message.success('')
        }
			})
		},
		// 
		addCancel(){
			this.$refs.artInfoRef.resetFields()
		},
		// 
    async $imgAdd(pos, $file) {
			var formdata = new FormData();
			formdata.append('file', $file);
			const {data: res} = await this.$http.post("/api/admin/file/",formdata,{ 'Content-Type': 'multipart/form-data' })
			this.$refs.md.$imglst2Url([[pos, "http://localhost:8080/"+res.data]])
    },
		async $imgDel(pos){
			var fid = pos[0].split("/")
			const {data: res} = await this.$http.delete("/api/admin/file/",{params:{"fid":fid[fid.length-1]}})
			if (res.code !== 200) return this.$message.error(res.msg)
			return this.$message.success("")
		}
	}

}
</script>

